.loader_wrapper {
  width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #242f3f;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    z-index: 10000;
    -webkit-animation: loader_wrapper 2s ease;
            animation: loader_wrapper 2s ease;
  }
  
  @-webkit-keyframes loader_wrapper {
  0% {opacity : 0;}
  50% {opacity : 0.7;}
  75% {opacity : 0.9;}
  100% {opacity : 1;}
}
  
  @keyframes loader_wrapper {
  0% {opacity : 0;}
  50% {opacity : 0.7;}
  75% {opacity : 0.9;}
  100% {opacity : 1;}
}

.loader {
  display: inline-block;
    width: 30px;
    height: 30px;
    position: relative;
    border: 4px solid #Fff;
    -webkit-animation: loader 2s infinite ease;
            animation: loader 2s infinite ease;
  }
  .loader_inner {
    vertical-align: top;
    display: inline-block;
    width: 100%;
    background-color: #fff;
    -webkit-animation: loader_inner 2s infinite ease-in;
            animation: loader_inner 2s infinite ease-in;
  }
  
  @-webkit-keyframes loader {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg);}
    25% { -webkit-transform: rotate(180deg); transform: rotate(180deg);}
    50% { -webkit-transform: rotate(180deg); transform: rotate(180deg);}
    75% { -webkit-transform: rotate(360deg); transform: rotate(360deg);}
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);}
  }
  
  @keyframes loader {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg);}
    25% { -webkit-transform: rotate(180deg); transform: rotate(180deg);}
    50% { -webkit-transform: rotate(180deg); transform: rotate(180deg);}
    75% { -webkit-transform: rotate(360deg); transform: rotate(360deg);}
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);}
  }
  
  @-webkit-keyframes loader_inner {
    0% { height: 0%;}
    25% { height: 0%;}
    50% { height: 100%;}
    75% { height: 100%;}
    100% { height: 0%;}
  }
  
  @keyframes loader_inner {
    0% { height: 0%;}
    25% { height: 0%;}
    50% { height: 100%;}
    75% { height: 100%;}
    100% { height: 0%;}
  }
